// =============================================================================
// AUDIT LOGGING
// =============================================================================
// Security audit trail for sensitive operations

import { createLogger } from './logger';

const logger = createLogger({ component: 'AuditLog' });

export type AuditAction = 
  | 'endpoint.create'
  | 'endpoint.update'
  | 'endpoint.delete'
  | 'endpoint.run'
  | 'api_key.create'
  | 'api_key.revoke'
  | 'plan.upgrade'
  | 'quota.exceeded'
  | 'auth.failed'
  | 'ssrf.blocked';

export interface AuditEvent {
  action: AuditAction;
  accountId?: string;
  apiKeyId?: string;
  resourceId?: string;
  resourceType?: string;
  ip?: string;
  userAgent?: string;
  details?: Record<string, any>;
  success: boolean;
  errorMessage?: string;
}

/**
 * Log an audit event for security tracking
 */
export function logAuditEvent(event: AuditEvent): void {
  const timestamp = new Date().toISOString();
  
  // Structured log for security monitoring
  logger.info({
    type: 'AUDIT',
    timestamp,
    action: event.action,
    accountId: event.accountId,
    apiKeyId: event.apiKeyId ? maskId(event.apiKeyId) : undefined,
    resourceId: event.resourceId,
    resourceType: event.resourceType,
    ip: event.ip,
    userAgent: event.userAgent?.substring(0, 100), // Truncate
    success: event.success,
    errorMessage: event.errorMessage,
    // Don't log full details in production, only in development
    details: process.env.NODE_ENV === 'development' ? event.details : undefined,
  }, `Audit: ${event.action} - ${event.success ? 'SUCCESS' : 'FAILED'}`);
}

/**
 * Mask sensitive IDs for logging
 */
function maskId(id: string): string {
  if (id.length <= 8) return '***';
  return id.substring(0, 4) + '...' + id.slice(-4);
}

/**
 * Log authentication failure (rate limit these logs to prevent log flooding)
 */
let authFailureCount = 0;
let lastAuthFailureLog = 0;

export function logAuthFailure(ip: string, reason: string): void {
  authFailureCount++;
  const now = Date.now();
  
  // Only log every 10 failures or every 60 seconds
  if (authFailureCount >= 10 || now - lastAuthFailureLog > 60000) {
    logger.warn({
      type: 'SECURITY',
      event: 'auth_failures',
      count: authFailureCount,
      ip,
      reason,
    }, `Auth failures: ${authFailureCount} attempts from ${ip}`);
    
    authFailureCount = 0;
    lastAuthFailureLog = now;
  }
}

