// =============================================================================
// BILLABLE ENDPOINT ROUTE: POST /v1/run/:endpointId
// =============================================================================
// Metered endpoint execution with quota enforcement and billing

import { NextRequest, NextResponse } from 'next/server';
import { requireAuth } from '@/lib/auth';
import { getDb } from '@/db/client';
import {
  getAccountPlanLimits,
  checkAndIncrementUsage,
  rateCall,
  logBillingEvent,
} from '@/lib/billing';
import { logAuditEvent } from '@/lib/audit-log';

interface EndpointRow {
  id: string;
  name: string;
  upstream_url: string;
  price_per_call: string;
  platform_fee_bps: number;
  account_id: string;
  status: string;
}

// Max request body size (1MB)
const MAX_BODY_SIZE = 1 * 1024 * 1024;

export async function POST(
  request: NextRequest,
  { params }: { params: Promise<{ endpointId: string }> }
) {
  const startTime = Date.now();
  const { endpointId } = await params;

  // 0. Check request size
  const contentLength = request.headers.get('content-length');
  if (contentLength && parseInt(contentLength, 10) > MAX_BODY_SIZE) {
    return NextResponse.json(
      { error: 'Request body too large', code: 'PAYLOAD_TOO_LARGE', maxSize: MAX_BODY_SIZE },
      { status: 413 }
    );
  }

  // 1. Authenticate
  const authResult = await requireAuth(request);
  if (authResult.response) {
    return authResult.response;
  }

  const { apiKey } = authResult;
  const accountId = apiKey.userId;

  if (!accountId) {
    return NextResponse.json(
      { error: 'No account associated with this API key', code: 'NO_ACCOUNT' },
      { status: 400 }
    );
  }

  // 2. Load endpoint and verify ownership
  const db = getDb();
  const endpointResult = await db.query<EndpointRow>(`
    SELECT id, name, upstream_url, price_per_call, platform_fee_bps, account_id, status
    FROM endpoints
    WHERE id = $1
  `, [endpointId]);

  if (endpointResult.rows.length === 0) {
    return NextResponse.json(
      { error: 'Endpoint not found', code: 'NOT_FOUND' },
      { status: 404 }
    );
  }

  const endpoint = endpointResult.rows[0];

  // Verify endpoint belongs to this account
  if (endpoint.account_id !== accountId) {
    return NextResponse.json(
      { error: 'Endpoint not found', code: 'NOT_FOUND' },
      { status: 404 }
    );
  }

  if (endpoint.status !== 'active') {
    return NextResponse.json(
      { error: 'Endpoint is not active', code: 'ENDPOINT_INACTIVE' },
      { status: 400 }
    );
  }

  // 3. Load account plan limits
  const limits = await getAccountPlanLimits(accountId);
  if (!limits) {
    return NextResponse.json(
      { error: 'No plan found for account', code: 'NO_PLAN' },
      { status: 400 }
    );
  }

  // 4. Check and increment usage (quota enforcement)
  const usageCheck = await checkAndIncrementUsage(accountId, limits);

  if (!usageCheck.allowed) {
    // Log quota exceeded event
    const rated = rateCall({
      pricePerCall: endpoint.price_per_call || '0.001',
      platformFeeBps: endpoint.platform_fee_bps || 200,
    });

    await logBillingEvent({
      accountId,
      endpointId,
      status: 'quota_exceeded',
      pricePerCall: rated.pricePerCall,
      platformFeeBps: endpoint.platform_fee_bps || 200,
      platformCut: rated.platformCut,
      creatorNet: rated.creatorNet,
      errorMessage: usageCheck.reason,
    });

    return NextResponse.json(
      {
        error: 'quota_exceeded',
        message: `You have exceeded your ${limits.name} plan quota. Upgrade your plan for higher limits.`,
        limits: {
          dailyLimit: limits.dailyLimit,
          monthlyLimit: limits.monthlyLimit,
        },
        usage: {
          daily: usageCheck.daily,
          monthly: usageCheck.monthly,
        },
      },
      { status: 429 }
    );
  }

  // 5. Rate the call
  const rated = rateCall({
    pricePerCall: endpoint.price_per_call || '0.001',
    platformFeeBps: endpoint.platform_fee_bps || 200,
  });

  // 6. Validate upstream URL (SSRF protection)
  const upstreamUrl = new URL(endpoint.upstream_url);
  
  // Block internal/private IPs
  const blockedHosts = ['localhost', '127.0.0.1', '0.0.0.0', '::1', 'metadata.google', '169.254.169.254'];
  const blockedPatterns = [/^10\./, /^172\.(1[6-9]|2[0-9]|3[01])\./, /^192\.168\./];
  
  if (blockedHosts.some(h => upstreamUrl.hostname.includes(h)) || 
      blockedPatterns.some(p => p.test(upstreamUrl.hostname))) {
    // Log security event
    logAuditEvent({
      action: 'ssrf.blocked',
      accountId,
      apiKeyId: apiKey.id,
      resourceId: endpointId,
      resourceType: 'endpoint',
      ip: request.ip || request.headers.get('x-forwarded-for')?.split(',')[0],
      success: false,
      errorMessage: `Blocked SSRF attempt to ${upstreamUrl.hostname}`,
      details: { attemptedHost: upstreamUrl.hostname },
    });
    
    return NextResponse.json(
      { error: 'Invalid upstream URL', code: 'SSRF_BLOCKED' },
      { status: 400 }
    );
  }

  // 7. Call upstream
  let upstreamResponse: Response | null = null;
  let upstreamStatusCode = 0;
  let upstreamError: string | null = null;
  let upstreamData: any = null;

  try {
    // Forward the request to upstream
    const body = await request.text();
    const headers: Record<string, string> = {};
    
    // Forward relevant headers (sanitized)
    const contentType = request.headers.get('content-type');
    if (contentType) headers['content-type'] = contentType;
    
    // Only forward upstream auth, never our internal auth
    const upstreamAuth = request.headers.get('x-upstream-auth');
    if (upstreamAuth) headers['authorization'] = upstreamAuth;
    
    // Add timeout to prevent hanging
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), 30000); // 30s timeout

    upstreamResponse = await fetch(endpoint.upstream_url, {
      method: 'POST',
      headers,
      body: body || undefined,
      signal: controller.signal,
    });
    
    clearTimeout(timeoutId);

    upstreamStatusCode = upstreamResponse.status;
    
    // Try to parse response as JSON
    const responseText = await upstreamResponse.text();
    try {
      upstreamData = JSON.parse(responseText);
    } catch {
      upstreamData = { raw: responseText };
    }
  } catch (err: any) {
    upstreamError = err.message || 'Upstream request failed';
    upstreamStatusCode = 502;
  }

  const responseTime = Date.now() - startTime;

  // 7. Log billing event (regardless of upstream success)
  const billingStatus = upstreamStatusCode >= 200 && upstreamStatusCode < 300 ? 'success' : 'failed';
  
  await logBillingEvent({
    accountId,
    endpointId,
    status: billingStatus,
    pricePerCall: rated.pricePerCall,
    platformFeeBps: endpoint.platform_fee_bps || 200,
    platformCut: rated.platformCut,
    creatorNet: rated.creatorNet,
    upstreamStatusCode,
    upstreamResponseTimeMs: responseTime,
    errorMessage: upstreamError || undefined,
  });

  // 8. Return response
  if (upstreamError) {
    return NextResponse.json(
      {
        error: 'upstream_error',
        message: upstreamError,
        billing: {
          charged: true,
          pricePerCall: rated.pricePerCall,
          platformCut: rated.platformCut,
          creatorNet: rated.creatorNet,
        },
        usage: {
          daily: usageCheck.daily,
          monthly: usageCheck.monthly,
        },
      },
      { status: 502 }
    );
  }

  return NextResponse.json({
    success: true,
    data: upstreamData,
    billing: {
      pricePerCall: rated.pricePerCall,
      platformCut: rated.platformCut,
      creatorNet: rated.creatorNet,
    },
    usage: {
      daily: usageCheck.daily,
      monthly: usageCheck.monthly,
      dailyLimit: limits.dailyLimit,
      monthlyLimit: limits.monthlyLimit,
    },
    meta: {
      endpointId,
      endpointName: endpoint.name,
      upstreamStatus: upstreamStatusCode,
      responseTimeMs: responseTime,
    },
  });
}

