// =============================================================================
// REDIS CLIENT
// =============================================================================
// Singleton Redis client for rate limiting and usage tracking

import { Redis } from '@upstash/redis';
import { createLogger } from './logger';

const logger = createLogger({ component: 'Redis' });

let redisClient: Redis | null = null;

/**
 * Get the singleton Redis client
 */
export function getRedisClient(): Redis {
  if (redisClient) {
    return redisClient;
  }

  const upstashUrl = process.env.UPSTASH_REDIS_REST_URL;
  const upstashToken = process.env.UPSTASH_REDIS_REST_TOKEN;

  if (!upstashUrl || !upstashToken) {
    logger.warn('Redis not configured - UPSTASH_REDIS_REST_URL or UPSTASH_REDIS_REST_TOKEN missing');
    // Return a mock client that does nothing for development
    return createMockRedis();
  }

  redisClient = new Redis({
    url: upstashUrl,
    token: upstashToken,
  });

  logger.info('Redis client initialized (Upstash)');
  return redisClient;
}

/**
 * Create a mock Redis client for development/testing without Redis
 */
function createMockRedis(): Redis {
  const storage = new Map<string, string>();
  
  return {
    get: async (key: string) => storage.get(key) || null,
    set: async (key: string, value: string) => {
      storage.set(key, value);
      return 'OK';
    },
    incr: async (key: string) => {
      const current = parseInt(storage.get(key) || '0', 10);
      const next = current + 1;
      storage.set(key, String(next));
      return next;
    },
    decr: async (key: string) => {
      const current = parseInt(storage.get(key) || '0', 10);
      const next = Math.max(0, current - 1);
      storage.set(key, String(next));
      return next;
    },
    expire: async () => 1,
    del: async (key: string) => {
      storage.delete(key);
      return 1;
    },
    multi: () => {
      const commands: Array<() => Promise<any>> = [];
      return {
        incr: (key: string) => {
          commands.push(async () => {
            const current = parseInt(storage.get(key) || '0', 10);
            const next = current + 1;
            storage.set(key, String(next));
            return next;
          });
          return { incr: () => {}, decr: () => {}, exec: async () => [] };
        },
        decr: (key: string) => {
          commands.push(async () => {
            const current = parseInt(storage.get(key) || '0', 10);
            const next = Math.max(0, current - 1);
            storage.set(key, String(next));
            return next;
          });
          return { incr: () => {}, decr: () => {}, exec: async () => [] };
        },
        exec: async () => {
          const results: Array<[Error | null, any]> = [];
          for (const cmd of commands) {
            try {
              const result = await cmd();
              results.push([null, result]);
            } catch (err) {
              results.push([err as Error, null]);
            }
          }
          return results;
        },
      };
    },
    ping: async () => 'PONG',
  } as unknown as Redis;
}

/**
 * Reset the Redis client (for testing)
 */
export function resetRedisClient(): void {
  redisClient = null;
}

